@import "normalizes.scss";
@import "resets.scss";

@font-face {
    font-family: "MontserratT";
    src: url('../fonts/Montserrat-Regular.ttf');
    font-weight: regular;
    font-style: normal; }

@font-face {
    font-family: "MontserratT";
    src: url('../fonts/Montserrat-SemiBold.ttf');
    font-weight: bold;
    font-style: normal; }

html {
    font-family: "MontserratT";
    font-weight: regular;
    scroll-behavior: smooth; }

* {
    box-sizing: border-box; }


$color-blue: #5290EA;
$color-orange: #F28B64;
$color-grey: #E2E2E2;


.container {
    max-width: 1140px;
    margin: 0 auto;
    position: relative; }

.main-section {
    position: relative;
    min-height: 800px;
    background-color: #F0F0F0;
    padding-top: 16px;
    overflow: hidden;
    &__nav-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        height: 69px;
        align-items: center; }
    &__content-container {
        position: relative;
        display: flex;
        flex-direction: row; }
    &__first-elipse {
        position: absolute;
        top: 129px;
        right: 80px; }
    &__second-elipse {
        position: absolute;
        right: -120px;
        top: 650px; } }

.nav-menu {
    &__list {
        display: flex;
        flex-direction: row; }
    &__list-item {
        font-weight: bold;
        margin-right: 64px;
        font-size: 18px;
        &:last-child {
            margin-right: 0px; }
        a {
            text-decoration: none;
            color: black;
            &:hover {
                color: $color-orange; } } } }



.left-wrapper {
    position: relative;
    &__rectengle-blue {
        position: absolute;
        top: 150px;
        left: -50px;
        width: 565.95px;
        height: 220px;
        background-color: $color-blue;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-64deg);
        z-index: 2; }
    &__rectengle-orange {
        position: absolute;
        width: 618.43px;
        height: 220px;
        background-color: $color-orange;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-50.81deg);
        left: -30px;
        top: 160px;
        z-index: 3; }
    &__rectengle-grey {
        position: absolute;
        width: 608.97px;
        height: 220px;
        background-color: $color-grey;
        border-radius: 100px;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(-36.15deg);
        left: 10px;
        top: 190px; }
    &__img {
        position: relative;
        z-index: 5; } }


.icons {
    display: flex;
    flex-direction: row; }

.right-wrapper {
    position: absolute;
    width: 372px;
    top: 150px;
    right: 76px;
    z-index: 5;
    &__icons-box {
        margin-bottom: 1px;
        padding-left: 10px; }
    &__title {
        margin-bottom: 5px; }

    &__text {
        font-size: 16px;
        margin-bottom: 20px; } }



.icons__item {
    margin-right: 17px;
    &:last-child {
        margin-right: 0px; } }


.title {
    font-size: 72px;
    position: relative;
    font-weight: bold;
    &__hr {
        width: 65px;
        position: absolute;
        margin: 0;
        bottom: 0px;
        border-top: 1px solid black; } }

.buttons {
    display: flex;
    flex-direction: row;
    &__main-btn {
        font-weight: bold;
        margin-right: 32px;
        width: 200px;
        height: 60px;
        background-color: $color-orange;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        border-radius: 100px;
        border: 0;
        color: white;
        font-size: 18px; }
    &__text {
        font-size: 16px; }
    &__icon-btn {
        display: flex;
        flex-direction: row;
        align-items: center;
        img {
            margin-right: 12px; } } }

.elipse {
    width: 194px;
    height: 194px;
    background: linear-gradient(143.92deg, #ed7a508e 14.85%, #4889ea93 54.17%, #2c2c2c8e 85.74%);
    border-radius: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    &_big {
        width: 282px;
        height: 282px;
        border-radius: 150px; }
    &__subtract {
        width: 132px;
        height: 132px;
        background: #F0F0F0;
        border-radius: 100px;
        &_white {
            background-color: #ffffff; }
        &_big {
            width: 188px;
            height: 188px; } } }

.second-section {
    min-height: 700px;
    background-color: #fff;
    padding-top: 32px;
    &__container {
        position: relative; }
    &__title {
        margin-bottom: 40px;
        &-hr {
            width: 94px;
            bottom: -12px; } }
    &__cards-box {
        position: relative;
        display: flex;
        flex-direction: row;
        margin-bottom: 28px;
        z-index: 999; }
    &__first-elipse {
        position: absolute;
        top: 341px;
        left: -70px; }
    &__icons-box {
        position: relative;
        z-index: 999; }
    &__second-elipse {
        position: absolute;
        top: 50px;
        right: -60px; }
    &__cards-item {
        margin-right: 30px;
        &:last-child {
            margin-right: 0px; } } }


.cards-item {
    display: flex;
    align-items: flex-end;
    position: relative;
    width: 360px;
    height: 360px;
    background-color: $color-grey;
    border-radius: 10px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    &__img {
        height: 335px; }
    &__text {
        font-weight: bold;
        width: 280px;
        font-size: 36px;
        text-align: center;
        transform: rotate(90deg);
        position: absolute;
        top: 144px;
        right: -45px; }
    &_orange {
        background-color: $color-orange; }
    &_blue {
        background-color: $color-blue; } }


.icons-box {
    display: flex;
    flex-direction: row;
    justify-content: space-around; }

.icons-item {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 98px;
    &__text {
        font-size: 18px;
        span {
            font-weight: bold;
            font-size: 48px; } }
    &__img {
        height: 80px;
        width: 80px;
        margin-right: 20px; } }

.third-section {
    position: relative;
    min-height: 640px;
    background-color: #F0F0F0;
    overflow: hidden;
    &__second-elipse {
        position: absolute;
        right: -118px;
        top: -120px; }
    &__container {
        min-height: 640px;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding: 0 23px 0 23px; }
    &__title-left {
        position: relative;
        padding-left: 155px;
        z-index: 999; }
    &__title-right {
        margin-bottom: 20px; }
    &__text {
        font-size: 18px;
        margin-bottom: 30px; }
    &__right-wrapper {
        max-width: 479px;
        padding-top: 46px; }
    &__left-wrapper {
        padding-top: 288px;
        position: relative; } }

.img-item__img {
    position: absolute;
    bottom: 161px; }

.title__hr-right {
    bottom: 75px; }

.footer-section {
    min-height: 280px;
    &__container {
        min-height: 280px;
        display: flex;
        flex-direction: row;
        justify-content: space-between; }
    &__title-right {
        padding-top: 39px;
        margin-bottom: 29px; }
    &__img {
        position: absolute;
        left: -250px;
        bottom: 0px; } }
